/* Put your site style here */
$sprk-using-webfonts: true;
$sprk-webfonts: (
    (
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Bold.woff2")
        format("woff2"),
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Bold.woff")
        format("woff")
    ),
    RocketSans,
    700
  ),
  (
    (
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Medium.woff2")
        format("woff2"),
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Medium.woff")
        format("woff")
    ),
    RocketSans,
    500
  ),
  (
    (
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Regular.woff2")
        format("woff2"),
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Regular.woff")
        format("woff")
    ),
    RocketSans,
    400
  ),
  (
    (
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Light.woff2")
        format("woff2"),
      url("https://www.rockomni.com/mcds/assets/GlobalContent/NonStockImages/Fonts/RocketSans-Light.woff")
        format("woff")
    ),
    RocketSans,
    300
  );

$sprk-masthead-border-bottom-size: 5px;

@import "node_modules/@sparkdesignsystem/spark-styles/_spark.scss";
